.categories {
    display: flex;
.wrapper {
    background-color: #fff;
    margin: auto;
    padding: 25px 40px 25px 40px;
    display: flex;


}
a {
    padding-left: 40px;
    padding-top: 15px;
    width: 180px;
    font-size: large;
    
}

    svg {
            width: 70px;
            height: 70px;
    }

    .wrapper:hover {
        fill: rgb(233, 31, 82);
        transition: all 0.2s ease-out;
        cursor: pointer;
        a span {
            border-bottom: 1px #039be5 solid;
        }
        
    }

}
